////////////////////////////////////////////////////////////////////////////////
// Color palette function
////////////////////////////////////////////////////////////////////////////////

$colors: (
'accent': (
  base: #a7c059
),
'primary': (
  light: lighten(#5e5a59, 10),
  base: #5e5a59,
  dark: darken(#5e5a59, 5),
),
 'grey': (
   dark: #333,
   base: #999,
   light: #ececec
 ),
 'black': (
   base: #000,
   trans: transparentize(#000, .3)
 ),
 'white': (
   base: #fff,
   trans: transparentize(#fff, .5)
 ),
 'social': (
   facebook: #3c5a9a,
   twitter: #55acee,
   google: #de4b39,
   reddit: #cee3f8,
   linkedin: #007bb5
 )
);

@function color($color-name, $tone: base) {
    @return map-get(map-get($colors, $color-name), $tone);
}

